body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.custom-header{
	margin-bottom: 0px;
}
.display-center{
	text-align: center;
}
#sidebar {
	min-width: 40px;
	max-width: 40px;
	min-height: vmax;
	background-color: aqua;
}
.display-mid{
	text-align: center
}
.custom-dashboard{
	margin-top: 40px;
	max-width: 900px;
}
.display-mid p{
	font-size: small;
	margin-bottom: 2px;
}
.custom-panel{
	min-width: 180px;
	max-height: 20vh;
	padding: 1px;

}
.custom-panel-body{
	text-align: center;
	padding: 5px;
	max-height: inherit;
}
p{
	font-size: xx-small;
	margin: 0;
}
h5{
	margin: 0;
}
.wrapper {
	display: flex;
	align-items: stretch;
}

